import React, { useState } from "react";
import { dbService, storageService, authService } from "fbase";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPencilAlt } from "@fortawesome/free-solid-svg-icons";
import dayjs from "dayjs";

const GTalk = ({ gObj, isOwner, userObj }) => {
    const [editing, setEditing] = useState(false);
    const [newtalk, setNewtalk] = useState(gObj.text);
    const onDeleteClick = async () => {
        const ok = window.confirm("Are you sure you wanto to delete this talk?");
        if (ok) {
            await dbService.doc(`GamjaTalk/${gObj.id}`).delete();
            await storageService.refFromURL(gObj.attachmentUrl).delete();
        }
    }
    const toggleEditing = () => setEditing((prev) => !prev);
    const onSubmit = async (event) => {
        event.preventDefault();
        await dbService.doc(`GamjaTalk/${gObj.id}`).update({
            text: newtalk
        });
        setEditing(false);
    }
    const onChange = (event) => {
        const {
            target: { value },
        } = event;
        setNewtalk(value);
    }
    return (
        <div className="nweet">
            {
                editing ? (
                    <>
                        {isOwner &&
                            (<>
                                <form onSubmit={onSubmit} className="container nweetEdit">
                                    <input type="text" placeholder="Edit your talk" value={newtalk} required autoFocus onChange={onChange} className="formInput" />
                                    <input type="submit" value="Update Talk" className="formBtn" />
                                </form>
                                <span onClick={toggleEditing} className="formBtn cancelBtn">
                                    Cancel
                                </span>
                            </>
                            )}
                    </>
                ) : (
                    <>
                        <div className="talker">From {gObj.displayName}</div>
                        <div className="talk">
                            <h4>{gObj.text}</h4>
                        </div>
                        <div className="date">{dayjs(gObj.createdAt).format("YYYY-MM-DD HH:mm:ss")}</div>
                        {gObj.attachmentUrl && <a href={gObj.attachmentUrl}><img src={gObj.attachmentUrl} /></a>}
                        {isOwner && (
                            <div className="nweet__actions">
                                <span onClick={onDeleteClick} className="trash">
                                    <FontAwesomeIcon icon={faTrash} />
                                </span>
                                <span onClick={toggleEditing}>
                                    <FontAwesomeIcon icon={faPencilAlt} />
                                </span>
                            </div>
                        )}
                    </>
                )}
        </div>
    )
}

export default GTalk;