import GTalk from "components/GTalk";
import { dbService } from "fbase";
import React, { useEffect, useState } from "react";
import Factory from "components/Factory";

const Home = ({ userObj }) => {
    const [GamjaTalk, SetGamjaTalk] = useState([]);
    useEffect(() => {
        dbService.collection("GamjaTalk").orderBy("createdAt", "desc").onSnapshot((snapshot) => {
            const talkArray = snapshot.docs.map((doc) => ({
                id: doc.id, ...doc.data(),
            }));
            SetGamjaTalk(talkArray);
        });
    }, []);

    return (
        <div className="container">
            <Factory userObj={userObj} />
            <div style={{ marginTop: 30 }}>
                {GamjaTalk.map((talk) => (
                    <GTalk key={talk.id} gObj={talk} isOwner={talk.creatorId === userObj.uid} userObj={userObj} />
                ))}
            </div>
        </div >
    )
}
export default Home;